import {initializeApp} from "firebase/app";
import {getAuth, onAuthStateChanged, createUserWithEmailAndPassword, signInWithEmailAndPassword} from "firebase/auth";
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import {Loader} from "@googlemaps/js-api-loader";

Function.define (Function.google.fire.base, "link", firebase, {configurable: true, enumerable: true, writable: true});
Function.define (Function.google.fire.base, "app", function (config) { return initializeApp (config); });
Function.define (Function.google.fire.base.app, "auth", function (app) { return getAuth (app); });
Function.define (Function.google.fire.base.app.auth, "state", function (auth, context) { return onAuthStateChanged (auth, function (account) { context.call (this, account) }); });
Function.define (Function.google.fire.base.app.auth, "email", function () {});
Function.define (Function.google.fire.base.app.auth.email, "register", function (auth, email, password) { return createUserWithEmailAndPassword (auth, email, password); });
Function.define (Function.google.fire.base.app.auth.email, "login", function (auth, email, password) { return signInWithEmailAndPassword (auth, email, password); });
Function.define (Function.google.fire.base.app.auth.email, "logout", function (auth) { return signOut (auth); });
Function.define (Function.google.map, "loader", function (apiKey) { return new Loader ({apiKey: apiKey}); });
